.navbar {
  box-sizing: border-box;
  width: 100%;
  height: 50px;
  background: #8c74ff;
  display: flex;
  justify-content: space-around;
  place-items: center;
  overflow: scroll;
  border-radius: 10px;
  box-shadow: 0px 0px 15px #8c74ff;
}
.navbar div {
  font-size: 30px;
  font-family: impact;
}
.sort {
  padding: 2px 10px;
  border-radius: 50px;
  color: #8c74ff;
  background: #ffffff;
}
::-webkit-scrollbar {
  display: none;
}

@media (max-width: 500px) {
  .navbar div {
    font-size: 18px;
  }
}
