.results {
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
}
.playerCard {
  width: 100%;
  margin: auto;
  max-width: 800px;
  box-sizing: border-box;
  background: #2a2a2a;
  color: #ffffff;
  border-radius: 100px 10px 10px 100px;
  display: flex;
  gap: 20px;
  padding: 10px;
  transition: all 0.5s;
}
.playerCard:hover {
  background: #3b3b3b;
}
.pos {
  display: grid;
  place-items: center;
  min-width: 80px !important;
  min-height: 80px !important;
  border-radius: 100%;
  font-size: 40px;
  font-family: Impact;
}
.teamData {
  width: 100% -100px;
  height: 80px;
}
.teamName {
  font-family: Impact;
  font-size: 25px;
}
.teamPlayers {
  color: #a1a1a1;
}

@media (max-width: 400px) {
  .pos {
    min-width: 70px !important;
    min-height: 70px !important;
  }
  .teamData {
    height: 70px;
  }
  .teamName {
    font-family: Impact;
    font-size: 18px;
  }
  .teamPlayers {
    font-size: 11px;
  }
}

.results {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.fade-in {
  opacity: 0;
  transform: translateY(200px);
  transition: opacity 1s ease-in-out, transform 1s ease-in-out;
}

.fade-in.visible {
  opacity: 1;
  transform: translateY(0);
}

.sorter {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: grid;
  place-items: center;
  backdrop-filter: blur(15px);
  animation: blur 0.5s linear alternate;
}
@keyframes blur {
  0% {
    backdrop-filter: blur(0px);
  }
  100% {
    backdrop-filter: blur(15px);
  }
}

.map {
  box-sizing: border-box;
  padding: 10px;
  width: 320px;
  min-height: 80vh;
  height: fit-content;
  background: #8c74ff;
  border-radius: 10px;
  font-family: impact;
}
.map div {
  box-sizing: border-box;
  padding: 10px;
  margin-bottom: 10px;
  width: 100%;
  border-radius: 10px;
  background: #1e1e1e;
}

.map div div {
  display: flex;
  justify-content: space-around;
  background: #3b3b3b;
  font-size: 18px;
}
.map div div button {
  padding: 2px 10px;
  border-radius: 10px;
  color: white;
  background: #8c74ff;
  width: fit-content;
  height: 30px;
  border: none;
  font-family: impact;
  font-size: 18px;
  cursor: pointer;
}
.map div p {
  margin: 0;
  margin-bottom: 10px;
}
.qrCode {
  position: fixed;
  right: 10px;
  bottom: 10px;
}
.qrCode img {
  width: 200px;
  height: 200px;
  border-radius: 20px;
  margin: 10px;
  padding: 5px;
  border: 5px solid #fff;
  animation: qr 10s linear infinite;
}
@keyframes qr {
  0% {
    transform: translateY(30px) translateX(200px) rotate(90deg);
  }
  5% {
    transform: translateY(-30px) translateX(-10px) rotate(-10deg);
  }
  10% {
    transform: translateY(0px) translateX(0px) rotate(0deg);
  }
  90% {
    transform: translateY(0px) translateX(0px) rotate(0deg);
  }
  100% {
    transform: translateY(400px) translateX(0px) rotate(-50deg);    
  }
}
.qrCode div {
  width: 100%;
  text-align: center;
  display: none;
}
.final {
  display: grid;
  place-items: center;
  font-size: 30px;
}
.xmark {
  scale: 1.5;
  padding-left: 5px;
}
@media (max-width: 700px) {
  .qrCode {
    display: none;
  }
}