body {
  margin: 0;
  padding: 10px;
  background: #1e1e1e;
  color: white;
}
.match {
  box-sizing: border-box;
  margin: 4vh;
  border-radius: 20px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  font-family: Impact;
}
.match span {
  font-size: 40px;
  animation: fade 1.5s linear alternate;
}
.match img {
  box-sizing: border-box;
  width: 100%;
  max-width: 800px;
  border-radius: 20px;
  filter: hue-rotate(20deg);
  animation: fade 1s linear alternate;
}
@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.info {
  display: flex;
  position: fixed;
  bottom: 5px;
  left: 0;
  width: 100%;
  color: #828282;
}
@media (max-width: 700px) {
  .info {
    display: none;
  }
}
